.CreateTask {
  text-align: center;
}

.CreateTask label {
  font-size: 24pt;
  color: black;
  display: block;
  padding: 10px;
  margin: 10px;
}

.CreateTask input,
.CreateTask textarea,
.CreateTask button {
  background: beige;
  font-size: 24pt;
  margin: 20px;
  vertical-align: middle;
  width: 300px;
  padding: 5px;
  border: 1px solid burlywood;
  border-radius: 10px;
}



